.stack_about{
	grid-template-columns: 1fr 1fr;

	.stack__item{
		max-width: 100%;
		min-width: 0px;
	}
}

.services{
	.section-description{
		max-width: 600px;
		width: 100%;
		font-size: 18px;
		border-top: 1px solid rgba(0,0,0,.1);
		margin: 0 auto;
		padding: 1rem 0;
	}

	.stack_services{
		margin-top: 20px;
		grid-template-columns: repeat(3, 1fr);
	}

	.stack__item{

	}

	.stack__icon{
		margin: 0 auto;
		width: 142px;
		height: 120px;
		background-repeat: no-repeat;
		background-image: url('../img/services.webp');

	}

	.stack__icon_technical_service{
		background-position: -13px -16px;
	}

	.stack__icon_repair{
		background-position: -171px -16px;
	}

	.stack__icon_disassembly{
		background-position: -22px -325px;
	}
}

.stack_news{
	grid-template-columns: repeat(4, 1fr);
}

.homepage-banner{
	height: 300px;
	background-size: cover;
	background-attachment: fixed;
}

.conctacts{
	background-attachment: fixed;
	background-size: cover
}

.stack_contacts{
	table{
		background: #fff;
		padding: 30px;

		tr > td:first-child{
			font-weight: 600;
		}

		td{
			padding: 4px 10px;
		}
	}	
}

.sto{
	.stack_sto{
		margin-top: 50px;
		li{
			margin: 7px 0;
			color: $accent_light;
			&.isnt{
				color: #999;
			}
			&:not(.isnt):hover{
				color: red;
				a{
					text-decoration: underline;
				}
			}
			i{
				margin-right: 10px;
			}
		}
	}
}